import * as dat from 'dat.gui';
import * as Tone from 'tone'
// import p5 from 'node-p5';

let x = 0;
let y = 0;
let z = 0;

let w = 0;
let t = 0;
let f = 0;

let p = 0
let g = 0

let mesh3;

let arpeggiator = []
let arp = []





// SYNTHESIZER (SOUNDS)
// SYNTHESIZER (SOUNDS)
// SYNTHESIZER (SOUNDS)

let analyzer = new Tone.FFT();

// const chorus = new Tone.Chorus(4, 2.5, 0.5).toDestination().start();
// chorus.connect(analyzer)

const pingPong = new Tone.PingPongDelay("8n", 0.001).toDestination();


const phaser = new Tone.Phaser({
	frequency: 100,
	octaves: 5,
    baseFrequency: 880
}).toDestination().connect(analyzer)

const synth = new Tone.MonoSynth({
	oscillator: {
		type: "square"
	},
	envelope: {
		attack: 0.1
	}
}).connect(analyzer).connect(phaser);

const synth1 = new Tone.MonoSynth({
	oscillator: {
		type: "square"
	},
	envelope: {
		attack: 0.1
	}
}).connect(phaser);

const synth3 = new Tone.MonoSynth({
	oscillator: {
		type: "triangle"
	},
	envelope: {
		attack: 0.1
	}
}).connect(pingPong);

const synth4 = new Tone.MonoSynth({
	oscillator: {
		type: "triangle"
	},
	envelope: {
		attack: 0.1
	}
}).toDestination();


//CONTROLS
//CONRTOLS

document.onkeydown = checkKey;

function checkKey(e) {



    e = e || window.event;

    if (e.keyCode == '81') {
        //Q
        synth.triggerAttackRelease("C4", "4n");
        synth1.triggerAttackRelease("A2", "4n");
        x = x + 1

        arpeggiator.push('C4')
    }
    else if (e.keyCode == '87') {
        // W
        synth.triggerAttackRelease("D4", "4n");
        synth1.triggerAttackRelease("G2", "4n");
        x = x / 2
        y = y + 0.5

        arpeggiator.push('D4')
    }
    else if (e.keyCode == '69') {
       // E
       synth.triggerAttackRelease("E4", "4n");
       synth1.triggerAttackRelease("D2", "4n");
       x = x + 0.5
       z = z / 3

       arpeggiator.push('E4')
    }
    else if (e.keyCode == '82') {
       // R
       synth.triggerAttackRelease("F4", "4n");
       synth1.triggerAttackRelease("G2", "4n");
       z = z - 0.5
       x = x + 1
       y = y + 1

       arpeggiator.push('F4')
    }
    else if (e.keyCode == '84') {
        // T
        synth.triggerAttackRelease("G4", "4n");
        synth1.triggerAttackRelease("E2", "4n");
        w = w + 20

        arpeggiator.push('G4')
     }
     else if (e.keyCode == '89') {
        // Y
        synth.triggerAttackRelease("A4", "4n");
        synth1.triggerAttackRelease("D2", "4n");
        t = t + 10
        f = f + 2
        x = x + 0.2
 
        arpeggiator.push('A4')
     }
     else if (e.keyCode == '85') {
        // U
        synth.triggerAttackRelease("B4", "4n");
        synth1.triggerAttackRelease("F2", "4n");
        t = t + 20 
        f = f + 15
        y = y + 0.5
 
        arpeggiator.push('B4')
     }
     else if (e.keyCode == '73') {
        // I
        synth.triggerAttackRelease("C5", "4n");
        synth1.triggerAttackRelease("D3", "4n");
        w = w + 5
        y = y + 1
        x = y * 2

        arpeggiator.push('C5')
     }
     else if (e.keyCode == '79') {
        // O
        synth.triggerAttackRelease("D5", "4n");
        synth1.triggerAttackRelease("F3", "4n");
        w = w + 10
        y = y + 0.5
        x = z * 2

        arpeggiator.push('D5')

     }
     else if (e.keyCode == '80') {
        // P
        synth.triggerAttackRelease("E5", "4n");
        synth1.triggerAttackRelease("B3", "4n");
        w = w + 5
        y = y + 5
        x = x + 2
        t = t + 5
        f = f + 1

        arpeggiator.push('E5')
     }

     else if (e.keyCode == '65') {
        // A
        synth3.triggerAttackRelease("D5", "4n");
        p = p + 10

        arpeggiator.push('D5')
        
    }
    else if (e.keyCode == '83') {
       // S
       synth3.triggerAttackRelease("E5", "4n");
       p = p + 50
       
       arpeggiator.push('E5')
    }
    else if (e.keyCode == '68') {
       // D
       synth3.triggerAttackRelease("F5", "4n");
       g = g + 1

       arpeggiator.push('F5')
 
    }
    else if (e.keyCode == '70') {
        // F
        synth3.triggerAttackRelease("G5", "4n");
        g = p / 10  + 2

        arpeggiator.push('G5')

     }
     else if (e.keyCode == '71') {
        // G
        synth3.triggerAttackRelease("A5", "4n");
        p = g / 20  + 5

        arpeggiator.push('A5')
 
     }
     else if (e.keyCode == '72') {
        // H
        synth3.triggerAttackRelease("B5", "4n");
        p = g + Math.floor(Math.random() * 100) 

        arpeggiator.push('B5')
 
     }
     else if (e.keyCode == '74') {
        // J
        synth3.triggerAttackRelease("C6", "4n");
        p = g + Math.floor(Math.random() * 500) 
        x = x + 2
        y = y + 0.5
        x = z * 2

        arpeggiator.push('C6')
     }
     else if (e.keyCode == '75') {
        // K
        synth3.triggerAttackRelease("D6", "4n");
        p = g + Math.floor(Math.random() * 2) 
        p = p + 10
        w = w +  Math.floor(Math.random() * 10) 

        arpeggiator.push('D6')

     }
     else if (e.keyCode == '76') {
        // L
        synth3.triggerAttackRelease("E6", "4n");
        x = x + 2
        t = t +  Math.floor(Math.random() * 10) 
 
     }
     else if (e.keyCode == '90' || e.keyCode == '88' || e.keyCode == '67' || e.keyCode == '86' || e.keyCode == '78' || e.keyCode == '77'){
         synth4.triggerAttackRelease(Math.floor(Math.random() * 400) , "1N")
         y = x + 2
         p = g + Math.floor(Math.random() * 2) 
         y = y + 0.5
         x = z * 2
     }

     if (e.keyCode == '13'){
        arp.push(arpeggiator)
        arpeggiator.length = 0;
        
     }
 console.log(arpeggiator)

    }





